@font-face {
  font-family: "NotoSansHans-Bold";
  src: url(/static/media/NotoSansHans-Bold1.1a1d2b4f.ttf), url(/static/media/NotoSansHans-Bold.be1ab001.eot);
}
@font-face {
  font-family: "NotoSansHans-Regular";
  src: url(/static/media/NotoSans-Regular1.0c22b1d8.ttf), url(/static/media/NotoSansHans-Regular.127a1713.eot);
}
@font-face {
  font-family: "NotoSansHans-Medium";
  src: url(/static/media/NotoSansHans-Medium.1d9e1675.ttf), url(/static/media/NotoSansHans-Medium.e257c7c1.eot);
}
.ant-drawer .ant-drawer-content {
  margin-bottom: 60px !important;
}
.config-panel {
  width: 100%;
}
.cloumnDiv {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
.mobileTabWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  z-index: 1001;
}
.mobileTabWrapper .columnWrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.mobileTabWrapper .columnWrapper span {
  font-family: "NotoSansHans-Regular", "HelveticaHelvetica", Arial, "Lucida Family", Verdana, Tahoma, "Trebuchet MS";
  font-size: 14px;
  color: #FFFFFF;
}
.mobileTabWrapper .SettingsIcon {
  position: absolute;
  left: 50px;
  cursor: pointer;
}
.mobileTabWrapper .SettingsIcon div {
  width: 24px;
  height: 24px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/bottombar/settings_white.png') center no-repeat;
  background-size: contain;
}
.mobileTabWrapper .SettingsIcon:hover div {
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/bottombar/settings_yellow.png') center no-repeat;
  background-size: contain;
}
.mobileTabWrapper .SettingsIcon:hover span {
  color: #F8E71C;
}
.mobileTabWrapper .ConfigurationIcon {
  cursor: pointer;
}
.mobileTabWrapper .ConfigurationIcon div {
  width: 24px;
  height: 24px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/bottombar/configuration_white.png') center no-repeat;
  background-size: contain;
}
.mobileTabWrapper .ConfigurationIcon:hover div {
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/bottombar/configuration_yellow.png') center no-repeat;
  background-size: contain;
}
.mobileTabWrapper .ConfigurationIcon:hover span {
  color: #F8E71C;
}
.mobileTabWrapper .ConfigurationDisabledIcon {
  cursor: not-allowed;
}
.mobileTabWrapper .ConfigurationDisabledIcon div {
  width: 24px;
  height: 24px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/bottombar/configuration_disabled.png') center no-repeat;
  background-size: contain;
}
.mobileTabWrapper .ConfigurationDisabledIcon span {
  color: #AB805B;
}
.mobileTabWrapper .DatafactsIcon {
  position: absolute;
  right: 50px;
  cursor: pointer;
}
.mobileTabWrapper .DatafactsIcon div {
  width: 24px;
  height: 24px;
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/bottombar/datafacts_white.png') center no-repeat;
  background-size: contain;
}
.mobileTabWrapper .DatafactsIcon:hover div {
  background: url('http://calliope-dev.oss-cn-shanghai.aliyuncs.com/image/images/bottombar/datafacts_yellow.png') center no-repeat;
  background-size: contain;
}
.mobileTabWrapper .DatafactsIcon:hover span {
  color: #F8E71C;
}
.bottomDrawers .ant-drawer-body {
  max-height: 85vh;
}
.MobileTabSettings .ant-divider-horizontal {
  margin: 0 !important;
}
